
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { getBars, deleteBar } from '@/api/'
import BpDialog from '../components/bpDialog'
export default {
data () {
return {
confirmVisible: false,
barList: [],
deleteInfo: null
}
},
beforeRouteEnter (to, from, next) {
document.title = '我的酒吧'
next()
},
created () {
getBars().then((res) => {
this.barList = res.result.hotelList
})
},
methods: {
confirmDelete () {
deleteBar({ht_id: this.deleteInfo.id}).then((res) => {
this.barList.splice(this.deleteInfo.index, 1)
this.confirmVisible = false
})
},
onDeleteBar (index, id) {
this.confirmVisible = true
this.deleteInfo = {
index: index,
id: id
}
},
goToBar (barId, barName) {
this.$router.push({
path: `/BusinessCenter/${barId}`,
query: {
name: encodeURI(barName)
}
})
}
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
BpDialog
}
}



